<template>
  <div>
    <div class="form">
      <div class="form-container">
        <h1>{{ $route.name }}</h1>
        <label for="email">{{ $t('label.email') }} : </label>
        <input type="text" id="email" name="email" v-model="email">
        <label for="password">{{ $t('label.password') }} : </label>
        <input type="password" id="password" name="password" v-model="password" @keyup.enter="login">
        <button @click="login">{{ $t('global.login') }}</button>
      </div>
      <p>{{ $t('global.noAccount') }} <a href="/register">{{ $t('global.createOne') }}</a></p>
    </div>
     <div class="alert" v-bind:class="{error : isError}" v-html="texterror"></div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  data () {
    return {
      email: '',
      password: '',
      isError: false,
      texterror: null
    }
  },
  methods: {
    ...mapActions('auth', { setAuth : 'setAuth' }),

    async login () {
      try {
        const resp = await this.setAuth({ identifier: this.email, password: this.password })
        if(!resp.jwt) {
          this.isError = true
          this.texterror = "Login or password is wrong"
        }
      }
      catch(err) {
          return err
        }
    }
  }
}
</script>

