<template>
  <div class="panel-global">
    <h1>{{title}}</h1>
    <div class="panel-listing">
      <itemCreatePanel />
      <itemPanel v-for="(serie, index) in series" :key="index" :serie="serie" class="panel-item"/>
    </div>
  </div>
</template>

<script>
import SeriesService from '@/services/SeriesService'
import itemCreatePanel from '@/components/itemCreatePanel'
import itemPanel from '@/components/itemPanel'
import { mapState, mapActions } from 'vuex'

export default {
  data () {
    return {
      titmeline: null,
      series: [],
      seriestats: [
        {
          title: 'Serie 1',
          platform: 'Netflix',
          category: 'Action',
          see: false
        },
        {
          title: 'Serie 2',
          platform: 'Prime Video',
          category: 'Horror',
          see: false
        },
        {
          title: 'Serie 3',
          platform: 'OCS',
          category: 'Science-fiction',
          see: true
        },
        {
          title: 'Serie 4',
          platform: 'Netflix',
          category: 'Anime',
          see: true
        }
      ]
    }
  },
  components: {
    itemPanel, itemCreatePanel
  },
  props: [
    'title'
  ],
  computed: {
    ...mapState('auth', ['isUserLoggedIn', 'account']),
    ...mapState('series', ['categories', 'platforms']),
  },
  methods: {
    ...mapActions('series', ['setCategories', 'setPlatforms'])
  },
  async mounted () {
      this.series = (await SeriesService.getSeries(this.account.user.id)).data
      this.setCategories()
      this.setPlatforms()
  }
}
</script>

<style lang="scss">
  @import "../scss/components/panel.scss";
</style>
