import axios from 'axios'
import SecureLS from "secure-ls";
const ls = new SecureLS()

export default () => {
  const token = ls.get('key');

  if(!token) {
    return axios.create({
      baseURL: `https://towatchseries.herokuapp.com/`
    });
  } else {
    return axios.create({
      baseURL: `https://towatchseries.herokuapp.com/`,
      headers: {
        'Authorization': `Bearer ${token.data}`
      }
    });
  }
}