<template>
  <div>
    <div class="form">
      <div class="form-container">
        <h1>{{ $route.name }}</h1>
        <label for="username">{{ $t('label.username') }} :</label>
        <input type="text" name="username" id="username" v-model="username">
        <label for="email">{{ $t('label.email') }} : </label>
        <input type="text" id="email" name="email" v-model="email">
        <label for="password">{{ $t('label.password') }} : </label>
        <input type="password" id="password" name="password" v-model="password" autocomplete="new-password" @keyup.enter="login">
        <button @click="register">{{ $t('global.register') }}</button>
      </div>
    <p>{{ $t('global.hasAccount') }} <a href="/login">{{ $t('global.login') }}</a></p>
    </div>
     <div class="alert" v-bind:class="{error : isError}" v-html="texterror"></div>
  </div>
</template>

<script>
import AuthenticationService from '@/services/AuthenticationService'

export default {
  name: 'register',
  data () {
    return {
      ursename: '',
      email: '',
      password: '',
      isError: false,
      texterror: null
    }
  },
  methods: {
    async register () {
      try {
        await AuthenticationService.register({
          username: this.username,
          email: this.email,
          password: this.password
        })
        this.$router.push({
          name: 'Home'
        })
      } catch (error) {
        this.isError = true
        this.texterror = "Une erreur est survenue"
      }
    }
  }
}
</script>

