import createPersistedState from 'vuex-persistedstate'
import SecureLS from "secure-ls";
const ls = new SecureLS()
import AuthenticationService from '@/services/AuthenticationService'
import router from '@/router'


const auth = {
    namespaced: true,
    strict: true,
    state: {
        account: null,
        isUserLoggedIn: false
    },
    mutations: {
        setDarkTheme(state) {
            state.isDarkTheme = !state.isDarkTheme
        },
        SET_AUTH(state, info) {
            if (info) {
                state.isUserLoggedIn = true
                state.account = info,
                ls.set('key', {data: info.jwt});
            } else {
                state.isUserLoggedIn = false
            }
            state.user = info
            router.push({name: 'Home'})
        },
        SET_UPDATE_PROFIL(state, info) {
            state.user.user = info
            router.push({name: 'Home'})
        }
    },
    actions: {
        setAuth({commit}, credentials) {
            AuthenticationService.login(credentials)
            .then(resp => {
                commit('SET_AUTH', resp.data)
            })
            .catch(err => {throw err})
        },
        setUpdateProfil({commit}, info) {
            AuthenticationService.updateProfile(info)
            .then(resp => {
                commit('SET_UPDATE_PROFIL', resp.data)
            })
            .catch(err => {throw err})
        }
    },
    plugins: [createPersistedState()],
}

export default auth;